import React, { useEffect, useState } from "react";
import Banner from "../components/banner";
import { Cases } from "../components/cases";
import IntroOverlay from "../components/introOverlay";
import gsap from "gsap";

const homeAnimation = (onAnimationComplete) => {
  //gsap timeline
  const tl = gsap.timeline();

  //title animation
  tl.from(".line span", {
    duration: 1.8,
    delay: 0.5,
    y: 100,
    ease: "power1.out",
    skewY: 7,
    stagger: {
      amount: 0.3, //.line span gets all elements
      // stagger puts a delay in animation between them
    },
  });

  //top boxes animation
  tl.to(".overlay-top", {
    duration: 1.6,
    height: 0,
    ease: "expo.inOut",
    stagger: 0.4,
  });

  //bottom boxes animation
  tl.to(".overlay-bottom", {
    duration: 1.6,
    width: 0,
    ease: "expo.inOut",
    delay: -0.8,
    stagger: 0.2,
  });

  //hide the overlays so that the clicks can work
  //tl.to(".intro-overlay", { duration: 0, css: { display: "none" } });

  //image zoom out effect
  tl.from(".case-img img", {
    duration: 1.6,
    scale: 1.4,
    ease: "expo.inOut",
    delay: -2.0,
    stagger: 0.4,
    onComplete: onAnimationComplete,
  });
};

function Home() {
  const [animationComplete, setAnimationComplete] = useState(false);

  const onAnimationComplete = () => {
    setAnimationComplete(true);
  };

  useEffect(() => {
    homeAnimation(onAnimationComplete);
  }, []);
  return (
    <>
      {animationComplete === false ? <IntroOverlay /> : ""}
      <Banner />
      <Cases />
    </>
  );
}

export default Home;
