import React, { useEffect, useState } from "react";
import { Route } from "react-router-dom";
import "./styles/App.scss";
import gsap from "gsap";

//components
import Header from "./components/header";
import Navigation from "./components/navigation";

//pages
import Home from "./pages/home";
import About from "./pages/about";
import Portfolio from "./pages/portfolio";
import Services from "./pages/services";
//Routes
const routes = [
  { path: "/", name: "Home", Component: Home },
  { path: "/about", name: "About", Component: About },
  { path: "/portfolio", name: "Portfolio", Component: Portfolio },
  { path: "/services", name: "Services", Component: Services },
];

//responsive after a sec, prevents to make app less heavy
function debounce(fn, ms) {
  let timer;
  return () => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, arguments);
    }, ms);
  };
}

function App() {
  //get viewport height, width
  const [dimensions, setDimensions] = useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });

  useEffect(() => {
    //gets viewport height and modify the value in scss files
    let vh = dimensions.height * 0.01;
    document.documentElement.style.setProperty("--vh", `${vh}px`);

    //to avoid white flash on frequent fast refreshes
    //also set body to visibility hidden in App.scss
    gsap.to("body", { duration: 0, css: { visibility: "visible" } });
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    }, 1000);

    window.addEventListener("resize", debouncedHandleResize);
    return () => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  }, [dimensions]);
  return (
    <>
      <Header dimensions={dimensions} />
      <div className="app">
        {routes.map(({ path, Component }) => (
          <Route key={path} exact path={path}>
            <Component dimensions={dimensions} />
          </Route>
        ))}
      </div>
      <Navigation />
    </>
  );
}

export default App;
